@import url(https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&family=Roboto:ital,wght@0,100..900;1,100..900&display=swap);
html {
  font-size: 16px;
}

.highlight-text {
  font-weight: bold;
  color: #32CD32;
}

.small-title{
  font-weight: 500;
  font-size: 1.5rem;
  letter-spacing: 9.18px;
  color: #1f4a7b;
}

.small-title-2{
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  color: #629e4a;
  letter-spacing: 9.18px;
  font-size: 1.5rem;
}

.title-2{
  color: #1f4a7b;
}

.text{
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
  color: #629e4a;
  font-size: 1.5rem;
}

body {
  font-family: "Inter", serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.introduction {
  background-image: url('/delpi-na-estrada/endless-road.jpg');
  background-size: cover; 
  background-position: center;
  width: 100%;
  height: 800px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 10px solid #32CD32;
}

.introduction::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 1, 5, 0.8);
  z-index: 1;
}

.sponsors{
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: space-around;
  grid-gap: 10px;
  gap: 10px;
  height: 70px;
  padding-bottom: 25px;
}

.sponsors img{
  width: 40%;
}

.content{
  z-index: 2;
  position: relative;
  font-size: 1.5rem;
  text-align: center;
}

.content h1 {
  color: #32CD32;
  font-size: 4.5rem;
  text-align: center;
}

.content p {
  color: #fff;
  font-size: 1.5rem;
  text-align: center;
}

.logo{
  width: 100%;
  position: relative;
  z-index: 2;
  height: auto;
  max-width: 900px;
}

.content .participe-btn{
  width: 400px;
  height: 60px;
  border: none;
  cursor: pointer;
  background-color: #32CD32 ;
  font-weight: 500;
  color: #17375c;
  border-radius: 4px;
  font-size:1.3rem;
  padding: 10px;
  transition-duration: .3s;
  transition-property: transform, background-color, color;
}

.content .participe-btn:hover{
  background-color: #fff;
  transform: scale(1.1);
}

.content .btn-text{
  color:#32CD32;
}

@media (max-width: 920px) {
  .logo{
    width: 90%;
  }  
}

@media (max-width: 768px) {

  .content h1 {
    font-size: 3rem;
  }

  .content p {
    font-size: 1.2rem;
  }

  .participe-btn {
    font-size: 1.1rem;
    padding: 8px;
  }
}

@media (max-width: 480px) {

  .content h1 {
    font-size: 2.5rem;
  }

  .content p {
    font-size: 1rem;
  }

  .participe-btn {
    font-size: 1rem;
    padding: 6px;
  }
}
.agenda{
  border-bottom: 10px solid #32CD32;
}

.container-agenda{
  width: 100%;
  background-color: #1c1b20;
}

.agenda-content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  padding-bottom:30px;
}

.agenda-content h2 {
  text-align: center;
  font-size: 2.9rem;
  text-transform: uppercase;
  color: #f5faff;
  padding:10px;
  margin: 0;
}

.agenda-table-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.agenda-table {
  box-shadow: 7px 5px 11px 0px rgba(0,0,0,0.6);
  width: 80%;
  border-collapse: separate;
  border-spacing: 0;
  border-radius: 6px;
  overflow: hidden;
}

.agenda-table th{
  font-size: 1.5rem;
  font-weight: 700;
}

.agenda-table th, .agenda-table td {
  padding: 12px;
  text-align: center;
  color: #f5faff;
}

.agenda-table th {
  background-color: #1f4a7b;
}

.agenda-table tr:nth-child(even) {
  background-color: #2a5a8a;
}

.agenda-table tr:nth-child(odd) {
  background-color: #1f4a7b;
}

@media (max-width: 906px) {
  .agenda-item{
    font-size: 1rem;  
  }
  .badge-gdk {
    width: 120px;
  }

  .agenda-content h2 {
    font-size: 2rem;
  }
}

@media (max-width: 774px) {
  .agenda-item{
    font-size: 0.8rem;
  }
  .badge-gdk {
    width: 100px;
    height: 40px;
  }

  .agenda-content h2 {
    font-size: 1.5rem;
  }
}
.locais{
  width: 100%;
  padding-bottom: 10px;
  height: auto;
  background-color: #f5faff;
  border-bottom: 10px solid #32CD32;
}

.container-locais{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.locais-content{
  width: 80%;
}

.locais-content h2{
  text-align: center;
  font-size: 2.7rem;
  padding: 10px;
  text-transform: uppercase;
  margin: 0;
}

.locais-card-container{
  width: 100%;
}

.local-card-group{
  display: flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap; 
}

.local-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
  width: 23%;
  height: 310px;
  background-color:#1c1b20;
  border-radius: 10px;
  box-shadow: 7px 5px 11px 0px rgba(0,0,0,0.6);
}

.local-card h3{
  color: #32CD32;
  text-align: center;
  font-size: 1.7rem;
  text-transform: uppercase;
}

.inscricao-local-btn{
  padding: 10px;
  height: 60px;
  margin: 0 auto;
  text-align: center;
  width: 95%;
  cursor: pointer;
  text-transform: uppercase;
  border: none;
  background-color: #32CD32 ;
  font-weight: 500;
  color: #17375c;
  border-radius: 4px;
  font-size:1.3rem;
  padding: 10px;
  transition-duration: .3s;
  transition-property: transform, background-color, color;
}

.inscricao-local-btn:hover{
  background-color: #fff;
}

@media (max-width: 1791px) {
  .local-card{
    width: 40%;
  } 
}

@media (max-width: 1031px) {
  .local-card{
    width: 100%;
  } 
}



.participe{
  width: 100%;
  padding-bottom: 10px;
  height: auto;
  background-color: #f5faff;
  border-bottom: 10px solid #32CD32;
}

.participe-container{
  width: 100%;
  padding-top:10px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.participe-content{
  width: 90%;
}

.participe-container h2{
  text-align: center;
  font-size: 2.7rem;
  padding: 10px;
  color: #1c1b20;
  text-transform: uppercase;
  margin: 0;
}

.participe-content p {
  color: #1c1b20;
  font-size: 1.5rem;
  text-align: left;
}

.card-content{
  border-radius:6px;
  margin-top: 10px;
  width: 100%;
  height: 300px;
  border: 3px solid #1c1b20;
}
